.mc__storage {
  position: relative;
  &__bg {
    position: absolute;
    inset: 0;
    margin-top: 50px;
    @include screen(tablet) {
      margin-top: 128px;
    }
    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
    &:before {
      content: "";
      position: absolute;
      inset: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.2);
      z-index: 2;
    }
  }
  &__wrapper {
    z-index: 3;
    margin-top: 50px;
    position: relative;
    padding-bottom: 54px;
    @include screen(tablet) {
      padding-bottom: 689px;
      margin-top: 128px;
    }
    h2 {
      position: relative;
      text-transform: uppercase;
      z-index: 2;
      position: relative;
      font-size: 32px;
      line-height: 30px;
      font-weight: 300;
      @include screen(tablet) {
        font-size: 82px;
        line-height: 1;
      }
      br {
        @include screen(tablet) {
          display: none;
        }
      }
      span {
        display: inline-block;
        position: relative;
        font-family: $DXRigrafEx;
        font-weight: 700;
      }
      .mc__line {
        &:before {
          content: "";
          position: absolute;
          z-index: -1;
          left: -2%;
          height: 20px;
          bottom: 0px;
          width: 104%;
          background-image: url(data:image/svg+xml;base64,);
          background-repeat: no-repeat;
          background-position: center;
          background-size: 100%;
          @include screen(tablet) {
            height: 40px;
            display: block;
            transform: scaleY(1.5);
            bottom: 0;
          }
        }
      }
      &.reversed {
        font-weight: 700;
        font-family: $DXRigrafEx;
        span {
          font-family: $DXRigraf;
          font-weight: 300;
        }
      }
    }
    p {
      font-family: $Inter;
      font-size: 14px;
      line-height: 20px;
      @include screen(tablet) {
        font-size: 24px;
        line-height: 36px;
        max-width: 100%;
      }
      br {
        @include screen(laptop) {
          display: none;
        }
      }
      br.pc {
        display: none;
        @include screen(laptop) {
          display: block;
        }
      }
    }
  }
  &__item {
    position: relative;
    @include screen(tablet) {
      max-width: 70%;
    }
    &:nth-child(1) {
      margin-top: 50px;
      @include screen(tablet) {
        margin-top: 244px;
      }
    }
    &:nth-child(2) {
      margin-top: 92px;
      margin-left: 30%;
      max-width: 80%;
      @include screen(tablet) {
        margin-top: 216px;
        margin-left: auto;
        max-width: 71%;
      }
    }
  }
  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    row-gap: 25px;
    @include screen(tablet) {
      position: relative;
    }
    img {
      position: absolute;
      bottom: -74px;
      left: 50%;
      width: 69px;
      height: 66px;
      transform: translateX(-50%);
      @include screen(tablet) {
        width: auto;
        height: auto;
        bottom: -244px;
        left: 40%;
      }
    }
  }
  &__stats {
    display: flex;
    column-gap: 27px;
    @include screen(tablet) {
      column-gap: 70px;
      margin-top: 50px;
    }
  }
  &__stat {
    h3 {
      font-family: $DXRigrafEx;
      font-weight: 700;
      font-size: 24px;
      line-height: 24px;
      @include screen(tablet) {
        font-size: 82px;
        line-height: 82px;
      }
    }
    p {
      font-size: 14px;
      text-transform: uppercase;
      font-family: $Inter;
      @include screen(tablet) {
        font-size: 24px;
      }
    }
  }
}