.mc__products {
  position: relative;
  &__bg {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    @include screen(tablet) {
      max-height: 100%;
    }
    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
      @include screen(tablet) {
        object-position: 0 45%;
      }
    }
  }
  &-info {
    position: relative;
    z-index: 3;
    padding: 50px 0 144px;
    @include screen(laptop) {
      padding: 120px 0 73px;
    }
    &__wrapper {
      h2 {
        position: relative;
        font-family: $DXRigrafEx;
        text-transform: uppercase;
        z-index: 2;
        position: relative;
        font-size: 32px;
        line-height: 36px;
        @include screen(laptop) {
          font-size: 42px;
          line-height: 48px;
        }
        br {
          @include screen(tablet) {
            display: none;
          }
        }
        .mc__line {
          &:before {
            content: "";
            position: absolute;
            bottom: -4px;
            z-index: -1;
            left: -2%;
            width: 104%;
            height: 20px;
            background-image: url(data:image/svg+xml;base64,);
            background-repeat: no-repeat;
            background-position: center;
            background-size: 100%;
            @include screen(tablet) {
              background-image: url(data:image/svg+xml;base64,);
            }
          }
        }
        span {
          position: relative;
          z-index: 2;
        }
      }
      ol {
        max-width: 448px;
        margin-top: 20px;
        counter-reset: section;
        display: flex;
        flex-direction: column;
        row-gap: 20px;
        position: relative;
        &:before {
          content: "";
          position: absolute;
          width: 100%;
          height: 100%;
          background: rgba(0, 0, 0, 0.5);
          filter: blur(30px);
          z-index: 2;
          @include screen(laptop) {
            background: rgba(0, 0, 0, 0.3);
          }
        }
        @include screen(laptop) {
          row-gap: 30px;
          margin-top: 48px;
        }
        li {
          counter-increment: section;
          font-size: 14px;
          position: relative;
          z-index: 2;
          font-family: $Inter;
          letter-spacing: 0.2px;
          &:before {
            content: counter(section) ". ";
          }
          br {
            @include screen(laptop) {
              display: none;
            }
          }
          @include screen(laptop) {
            font-size: 24px;
            line-height: 30px;
          }
        }
      }
    }
  }
  &-reviews {
    position: relative;
    z-index: 3;
    background-color: $purple;
    padding: 28px 0 44px;
    @include screen(laptop) {
      background-color: $purple-dark;
      padding: 28px 0 179px;
    }
    .swiper {
      @include screen(laptop) {
        max-width: 469px;
        margin: 0 0 0 32px;
      }
      &-slide {
        @include screen(laptop) {
          min-height: 227px;
        }
      }
    }
    &__link {
      margin-top: 20px;
      display: inline-block;
      position: relative;
      transition: color $transition-default;
      &:hover {
        color: $red;
      }
      &:before {
        content: "";
        position: absolute;
        bottom: -5px;
        left: 0;
        width: 100%;
        height: 5px;
        background-image: url(data:image/svg+xml;base64,);
        background-repeat: no-repeat;
        background-position: center;
        background-size: 100%;
      }
      @include screen(laptop) {
        margin-left: 32px;
      }
    }
    &__rating {
      display: flex;
      column-gap: 6px;
      align-items: center;
    }
    &__title {
      margin-top: 15px;
      display: flex;
      align-items: center;
      column-gap: 4px;
      @include screen(laptop) {
        margin-top: 20px;
      }
      span {
        font-size: 20px;
        font-family: $DXRigrafEx;
        line-height: 22px;
      }
    }
    &__text {
      margin-top: 12px;
      font-family: $Inter;
      line-height: 22px;
      display: flex;
      flex-direction: column;
      row-gap: 16px;
      br {
        display: none;
        @include screen(laptop) {
          display: block;
        }
      }
      @include screen(laptop) {
        max-width: 469px;
      }
    }
  }
  &-items {
    background-color: $purple-dark;
    position: relative;
    z-index: 5;
    padding: 0 0 53px;
    @include screen(laptop) {
      background-color: transparent;
      position: absolute;
      width: 57%;
      overflow: visible;
      padding-bottom: 0;
      bottom: 76px;
      right: 50px;
    }
    &__substrate {
      position: absolute;
      bottom: 95px;
      width: 761px;
      transform: translateX(-50%);
      left: 50%;
      @include screen(laptop) {
        bottom: 120px;
      }
      @include screen(laptop) {
        display: none;
      }
    }
    &__buttons {
      display: flex;
      margin-top: 30px;
      width: 100%;
      align-items: center;
      padding: 0 16px;
      flex-direction: column;
      row-gap: 20px;
      @include screen(laptop) {
        column-gap: 25px;
        justify-content: center;
        flex-direction: row;
        padding: 0;
      }
      .mc__button {
        padding: 16px;
        width: 100%;
        max-width: 350px;
        @include screen(laptop) {
          width: auto;
          max-width: 100%;
          padding: 18px 32px;
        }
      }
    }
    .swiper {
      transform: translateY(-102px);
      user-select: none;
      margin-bottom: -102px;
      &-wrapper {
        align-items: stretch;
      }
      @include screen(laptop) {
        margin-bottom: 0;
        overflow: visible;
        transform: translateY(0);
      }
      &-slide {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        flex-direction: column;
        height: auto;
        img {
          max-width: 90%;
          min-height: 380px;
          object-fit: contain;
        }
      }
      &-button {
        &-prev,
        &-next {
          top: 40%;
          transform: translateY(-50%);
          margin-top: 0;
          @include screen(laptop) {
            top: 50%;
            transform: translateY(-25%);
          }
          &:before,
          &:after {
            display: none;
          }
        }
        &-prev {
          width: 30px;
          height: 30px;
          @include screen(laptop) {
            width: auto;
            height: auto;
          }
        }
        &-next {
          width: 50px;
          height: 50px;
          @include screen(laptop) {
            width: auto;
            height: auto;
          }
        }
      }
    }
  }
}