.mc__formula {
  padding: 80px 0;
  position: relative;
  @include screen(tablet) {
    padding: 119px 0 114px;
  }
  &__bg {
    position: absolute;
    inset: 0;
    overflow: hidden;
    img {
      object-fit: cover;
      height: 100%;
      object-position: 100% 100%;
      transform: scale(1.8);
      transform-origin: bottom right;
      width: 100%;
      @include screen(tablet) {
        transform: scale(1.5);
        object-position: 50% 100%;
        transform-origin: bottom;
      }
    }
    &:before {
      content: "";
      position: absolute;
      inset: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.4);
      z-index: 2;
    }
  }
  &__wrapper {
    row-gap: 12px;
    flex-direction: column;
    justify-content: center;
    display: flex;
    z-index: 2;
    position: relative;
    @include screen(tablet) {
      row-gap: 30px;
    }
    h2 {
      position: relative;
      font-family: $DXRigrafEx;
      text-transform: uppercase;
      display: flex;
      justify-content: center;
      @include screen(tablet) {
        justify-content: flex-start;
        max-height: 198px;
      }
      img {
        z-index: 2;
        position: relative;
      }
      span {
        z-index: 2;
        position: relative;
        font-size: 32px;
        line-height: 36px;
        @include screen(tablet) {
          font-size: 42px;
          line-height: 48px;
        }
      }
    }
  }
  &__content {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    @include screen(tablet) {
      width: 51%;
      align-items: flex-start;
      margin-left: auto;
    }
    img {
      position: absolute;
      left: -161px;
      top: 0;
      display: none;
      @include screen(tablet) {
        display: block;
      }
    }
    p {
      font-size: 14px;
      z-index: 2;
      position: relative;
      margin-top: 12px;
      font-family: $Inter;
      text-align: center;
      &:first-child {
        margin-top: 0;
      }
      br {
        display: none;
        @include screen(tablet) {
          display: block;
        }
      }
      @include screen(tablet) {
        margin-top: 24px;
        font-size: 24px;
        text-align: left;
        line-height: 120%;
        &:first-child {
          margin-top: 0;
        }
      }
    }
    .mc__button {
      z-index: 2;
      position: relative;
      margin-top: 32px;
      max-width: 273px;
      width: 100%;
      @include screen(tablet) {
        max-width: 100%;
        width: auto;
      }
    }
  }
}